            .header {
                &__bar {
                    display: none;
                }
                // menu bar gizliligi div olarak
                display: flex;
                margin-top: 10px;
                &__logo {
                    a {
                        img {
                            width: 93px;
                        }
                    }
                }
                &__menu {
                    margin-left: auto;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    ul {
                        .buttons {
                            min-width: 180px;
                        }
                        display: flex;
                        list-style-type: none;
                        align-items: center;
                        li {
                            margin-right: 31px;
                            a {
                                text-decoration: none;
                                color: #000;
                                font-size: 22px;
                                font-weight: 650;
                            }
                        }
                    }
                }
            }
            
            // end css style header
            // media query style start
            @media (max-width: 991px) {
                .header {
                    &__bar {
                        display: block;
                    }
                    &__bars {
                        i {
                            border: 1px solid;
                            border-radius: 2px;
                            padding: 5px 15px;
                        }
                        li {
                            list-style-type: none;
                        }
                    }
                    nav {
                        display: none;
                    }
                }
            }
            
            @media (max-width: 767px) {
                .header {
                    &__main {
                        position: relative;
                    }
                    &__bar {
                        display: block;
                        position: absolute;
                        right: 40px;
                        // button icin verilmish right
                    }
                    &__bars {
                        i {
                            border: 1px solid;
                            border-radius: 2px;
                            padding: 5px 15px;
                        }
                    }
                }
            }